	module.exports = {"data":{"post":{"html":"<p><strong>I don't get 5 weeks vacation.</strong> Or 6. Or 4. In Germany there's a <a href=\"http://de.wikipedia.org/wiki/Bundesurlaubsgesetz\"><em>Bundesurlaubsgesetz</em></a> that regulates vacation time for employees. The legal minimum is 4 weeks per year, although most industries have contracts that call for more. Our employees get 5 to 6 weeks vacation, depending on longevity, as set in our <a href=\"http://www.bundesverband-systemgastronomie.de/ueber-den-bds/tarifabschluss.html\"><em>Manteltarifvertrag</em></a>.</p>\n<p>But as a business owner you're on your own. And our business tends to be busiest when other people are enjoying themselves (weekends, school vacations and holidays). We tend to take a couple days off time and again rather than a bunch of days in one piece. The work is always waiting for us when we get back.</p>\n<p>Next week, though, we are heading to Minnesota for a couple of weeks. The cell phones are staying home.</p>\n<p>(part of a <a href=\"http://www.papascott.de/archives/category/not-an-expat/\">series</a>)</p>","fields":{"title":"I am not an Expat Blogger 4: Vacation","date":"23 Jul 2012","url":"https://papascott.de/2012-07-23-i-am-not-an-expat-blogger-4-vacation/"}}},"pathContext":{"slug":"/2012-07-23-i-am-not-an-expat-blogger-4-vacation/","prev":{"fields":{"title":"Fire Camp","date":"21 Jul 2012","slug":"/2012-07-21-fire-camp/"}},"next":{"fields":{"title":"I am not an Expat Blogger 5: Public Transportation","date":"24 Jul 2012","slug":"/2012-07-24-i-am-not-an-expat-blogger-5-public-transportation/"}}}}

